.page1container {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	border: 2px solid red;
	overflow: visible;
	height: 99%;
	padding: 2%;
	background-color: aliceblue;
}

.usernamelabel {
	padding: 0 10px 0 0;
}

.passwordlabel {
	padding: 0 14px 0 0;
}

.loginbutton {
	border: 2px solid blue;
	padding: 10px;
	cursor: pointer;
	background-color: #ddd;
	margin: 0 0 0 300px;
}

.loginbutton:hover {
	background-color: gold;
}

.tabs {
	border: 2px solid blue;
	overflow: visible;
	background-color: burlywood;
}

.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}

.tablinks {
  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
}

.tab button:hover {
  background-color: #ddd;
}

.tablinksa {
  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  background-color: #ccc;
}

.tabcontent {
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-top: none;
}