.tabscontainer {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	border: 2px solid red;
	overflow: visible;
	height: 99%;
	padding: 2%;
	background-color: aliceblue;
}

.tabs {
	border: 2px solid blue;
	overflow: visible;
	background-color: burlywood;
}

.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}

.tablinks {
  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
}

.tab button:hover {
  background-color: #ddd;
}

.tablinksa {
  background-color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  background-color: #ccc;
}

.tabcontent {
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-top: none;
}